body {
  background-color: blue;
  color: white;
  font-family: "Massimo", monospace;
  
}

a:link {
  color: black;
  background-color: #EEE553;
  padding-top: 5px;
  padding-bottom: 5px;

}

a:hover {
  color: #F9939A;
}

#runnin {

  width: 200px;
}

#organize {
  display: flex;
}

#organize h1 {
  font-size: 130px;
  font-family: "Massimo";
}

@font-face {
  font-family: Massimo;
  src: url(/static/media/MassimoGrafia-Regular.5c2e1b6f.woff);
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

